import React, { useState } from 'react';
import { Plus, Check, ShoppingCart, Package } from 'lucide-react';
import type { ShoppingItem } from '../types';

const MOCK_CATEGORIES = [
  'Supplements',
  'Proteins',
  'Vegetables',
  'Fruits',
  'Grains',
  'Dairy',
  'Pantry',
];

const MOCK_ITEMS: ShoppingItem[] = [
  {
    id: '1',
    name: 'Omega-3 Fish Oil',
    amount: 1,
    unit: 'bottle',
    category: 'Supplements',
    purchased: false,
    productLink: 'https://example.com/fish-oil'
  },
  {
    id: '2',
    name: 'Chicken Breast',
    amount: 2,
    unit: 'lbs',
    category: 'Proteins',
    purchased: false
  },
  // Add more mock items
];

const ShoppingList: React.FC = () => {
  const [activeCategory, setActiveCategory] = useState<string>('all');
  const [items, setItems] = useState<ShoppingItem[]>(MOCK_ITEMS);
  const [newItemName, setNewItemName] = useState('');

  const handleAddItem = () => {
    if (!newItemName.trim()) return;

    const newItem: ShoppingItem = {
      id: Date.now().toString(),
      name: newItemName,
      amount: 1,
      unit: 'item',
      category: 'Pantry',
      purchased: false
    };

    setItems([...items, newItem]);
    setNewItemName('');
  };

  const toggleItemPurchased = (itemId: string) => {
    setItems(items.map(item =>
      item.id === itemId
        ? { ...item, purchased: !item.purchased }
        : item
    ));
  };

  const filteredItems = activeCategory === 'all'
    ? items
    : items.filter(item => item.category === activeCategory);

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-semibold text-gray-900">Shopping List</h1>
        <div className="flex items-center space-x-4">
          <button className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 flex items-center">
            <ShoppingCart className="w-5 h-5 mr-2" />
            Order All Supplements
          </button>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        <div className="lg:col-span-2">
          <div className="bg-white rounded-xl shadow-sm overflow-hidden">
            <div className="p-4 border-b border-gray-200">
              <div className="flex items-center space-x-4">
                <div className="flex-1">
                  <input
                    type="text"
                    placeholder="Add new item..."
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                    value={newItemName}
                    onChange={(e) => setNewItemName(e.target.value)}
                    onKeyPress={(e) => e.key === 'Enter' && handleAddItem()}
                  />
                </div>
                <button
                  onClick={handleAddItem}
                  className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 flex items-center"
                >
                  <Plus className="w-5 h-5 mr-2" />
                  Add
                </button>
              </div>
            </div>

            <div className="p-4">
              <div className="flex space-x-4 mb-6 overflow-x-auto">
                <button
                  className={`px-4 py-2 text-sm font-medium rounded-lg whitespace-nowrap ${
                    activeCategory === 'all'
                      ? 'bg-blue-50 text-blue-700'
                      : 'text-gray-500 hover:text-gray-700'
                  }`}
                  onClick={() => setActiveCategory('all')}
                >
                  All Items
                </button>
                {MOCK_CATEGORIES.map(category => (
                  <button
                    key={category}
                    className={`px-4 py-2 text-sm font-medium rounded-lg whitespace-nowrap ${
                      activeCategory === category
                        ? 'bg-blue-50 text-blue-700'
                        : 'text-gray-500 hover:text-gray-700'
                    }`}
                    onClick={() => setActiveCategory(category)}
                  >
                    {category}
                  </button>
                ))}
              </div>

              <div className="space-y-2">
                {filteredItems.map(item => (
                  <div
                    key={item.id}
                    className={`flex items-center justify-between p-3 rounded-lg ${
                      item.purchased ? 'bg-gray-50' : 'bg-white'
                    }`}
                  >
                    <div className="flex items-center space-x-3">
                      <button
                        onClick={() => toggleItemPurchased(item.id)}
                        className={`w-6 h-6 rounded-full border-2 flex items-center justify-center ${
                          item.purchased
                            ? 'border-green-500 bg-green-500'
                            : 'border-gray-300'
                        }`}
                      >
                        {item.purchased && (
                          <Check className="w-4 h-4 text-white" />
                        )}
                      </button>
                      <span className={item.purchased ? 'line-through text-gray-500' : ''}>
                        {item.amount} {item.unit} {item.name}
                      </span>
                    </div>
                    {item.productLink && (
                      <a
                        href={item.productLink}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="text-blue-600 hover:text-blue-700"
                      >
                        <Package className="w-5 h-5" />
                      </a>
                    )}
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>

        <div className="space-y-6">
          <div className="bg-white p-6 rounded-xl shadow-sm">
            <h2 className="text-lg font-medium text-gray-900 mb-4">
              Shopping Summary
            </h2>
            <div className="space-y-4">
              <div className="flex justify-between items-center">
                <span className="text-gray-500">Total Items</span>
                <span className="font-medium">{items.length}</span>
              </div>
              <div className="flex justify-between items-center">
                <span className="text-gray-500">Items Purchased</span>
                <span className="font-medium">
                  {items.filter(item => item.purchased).length}
                </span>
              </div>
              <div className="flex justify-between items-center">
                <span className="text-gray-500">Remaining Items</span>
                <span className="font-medium">
                  {items.filter(item => !item.purchased).length}
                </span>
              </div>
            </div>
          </div>

          <div className="bg-white p-6 rounded-xl shadow-sm">
            <h2 className="text-lg font-medium text-gray-900 mb-4">
              Recommended Supplements
            </h2>
            <div className="space-y-4">
              <div className="p-4 bg-blue-50 rounded-lg">
                <div className="flex justify-between items-start">
                  <div>
                    <h3 className="font-medium text-blue-900">Vitamin D3</h3>
                    <p className="text-sm text-blue-700 mt-1">
                      Running low - reorder recommended
                    </p>
                  </div>
                  <button className="text-blue-600 hover:text-blue-700">
                    <Plus className="w-5 h-5" />
                  </button>
                </div>
              </div>
              {/* Add more recommendations */}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ShoppingList; 